import axios from '../utils/request';
export const getList = (data: any): Promise<any> => {
  return axios.request({
    url: "/getList",
    method: "POST",
    data,
  });
};
export const getData = (params: any): Promise<any> => {
  return axios.request({
    url: "/getData",
    method: "GET",
    params,
  });
};