import React, { Component } from "react";
import { Button } from "antd";
import { connect } from "react-redux";
import action from "../../store/actions";
import { getList } from "../../apis/demo";
import { RouteComponentProps } from "react-router-dom";
interface IProps extends RouteComponentProps{
  count?:number;
  status:number;
  demoTest?:any;
}
class Demo extends Component<IProps> {
  componentDidMount() {}
  render() {
    // console.log(this.props);
    const {count,demoTest}=this.props;
    const {status}=this.props;
    return (
      <div>
        <div>Demo</div>
        <p>{count}</p>
        <Button onClick={()=>{demoTest({params:status-1});}}>+</Button>
        <Button
          onClick={async () => {
            const res = await getList({});
            console.log(res.data);
          }}
        >
          获取数据
        </Button>
      </div>
    );
  }
}

interface IMapStateToProps {
  demo: object;
}
export default connect((state:IMapStateToProps)=>state.demo,action.demo)(Demo);
